import React, { Component, PropTypes } from 'react'
import Navigation from './common/Navigation'

class App extends Component {
    render() {
        return (
            <div>
                {this.props.children}
            </div>
        )
    }
}

App.propTypes = {
    children: PropTypes.object.isRequired
}

export default App



